import { Fragment, useEffect } from "react";

import "bootstrap/dist/css/bootstrap.min.css";
import Button from "react-bootstrap/Button";
import Card from "react-bootstrap/Card";
import Container from "react-bootstrap/Container";
import Table from 'react-bootstrap/Table';

import { Image } from "react-bootstrap";
import { run as runHolder } from "holderjs/holder";

import NumberFormat from 'react-number-format';

import React, { useState} from 'react'

import './pages.css';


export default function MioLeaders() {
  
  const [mioLeaders, initMioLeaders] = useState([])
  const fetchData = async () => {
    const response = await fetch('https://wax.api.atomicassets.io/atomicassets/v1/accounts?collection_name=upliftium.hi&schema_name=upliftium&page=1&limit=50&order=desc')
    if (!response.ok) {
      throw new Error('Data coud not be fetched!')
    } else {
      return response.json()
    }
  }
  useEffect(() => {
    fetchData()
      .then((res) => {
        initMioLeaders(res.data)
        //console.log(res);
       // liftiumArray = res;
      })
      .catch((e) => {
        console.log(e.message)
      })
  }, [])


useEffect(() => {
    runHolder("image-class-name");
  });


console.log(mioLeaders);

  return (
    <Table bsPrefix="custom-table" striped bordered hover responsive>
    <thead>
        <tr>
          <th>#</th>
          <th>Account</th>
          <th>Amount</th>
        </tr>
      </thead>
      <tbody>
      {[...mioLeaders]
        .sort((a, b) => b.assets - a.assets)
        .slice(0, 25).map((item,idx) => {
        //let amount = item[1].toLocalString();
        return (
          <tr key={idx+'rank'}>
          <td>{idx+1}</td>
          <td>{item.account}</td>
          <td><NumberFormat thousandSeparator={true} displayType={'text'} value={item.assets} decimalScale={2} /></td>
        </tr>
        )
      })}
     
         </tbody>
    </Table>
    
  )
 
};
