import Container from "react-bootstrap/Container";
import Nav from "react-bootstrap/Nav";
import Navbar from "react-bootstrap/Navbar";
import NavDropdown from "react-bootstrap/NavDropdown";
import { NavLink } from "react-router-dom";
import "bootstrap/dist/css/bootstrap.min.css";
import "./nav.css"

function NavigationBar() {
  return (
    <Navbar expand="lg" bg="bg" variant="dark">
      <Container>
        <Navbar.Brand as={NavLink} to="/rewards">
          Uplift Information
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="me-auto" >
            <NavDropdown title="Rewards Leaderboard" id="basic-nav-dropdown" >
              <NavDropdown.Item as={NavLink} to="/leadersW1">S7 Week 1</NavDropdown.Item>
              <NavDropdown.Item as={NavLink} to="/leadersW2"> S7 Week 2</NavDropdown.Item>
              <NavDropdown.Item as={NavLink} to="/leadersW3">S7 Week 3</NavDropdown.Item>

              <NavDropdown.Item  as={NavLink} to="/leadersW4">S7 Week 4</NavDropdown.Item>
            </NavDropdown>
            <NavDropdown title="Mio/Liftium Stats" id="basic-nav-dropdown">
              <NavDropdown.Item as={NavLink} to="/liftium">
                Liftium Leaders
              </NavDropdown.Item>
              <NavDropdown.Item as={NavLink} to="/mio">
                Mio Leaders
              </NavDropdown.Item>
            </NavDropdown>
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
}

export default NavigationBar;
