
import Leaders from "./rewards/upliftiumRewardsS7W3";

import "bootstrap/dist/css/bootstrap.min.css";

import Container from "react-bootstrap/Container";
import Table from "react-bootstrap/Table";

import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";

import NumberFormat from "react-number-format";

import React, { useState } from "react";

import './pages.css';

export default function leadersW3() {
  console.log(Leaders);
  let landRewards =  [];
  let playerRewards = [];
  let pr = 0;
  let lr = 0;


  const playerObject = {
    account:"",
    p_id:"",
    amount:0,
    type:""
}
  
  Leaders.forEach(player =>{
    const player_temp = Object.create(playerObject);
      if(player.type === 'playerRewards'){
        player_temp.account = player.playerWallet;
        player_temp.p_id = player.minecraftUUID;
        player_temp.amount = player.amount;
        player_temp.type = player.type;

        playerRewards[pr] = player_temp;
        pr++;

      }else{
        player_temp.p_id = player.minecraftUUID;
        player_temp.amount = player.amount;
        player_temp.type = player.type;

        landRewards[lr] =player_temp;
        lr++;

      }
});
/*
  useEffect(() => {
    runHolder("image-class-name");
  });*/
  console.log(playerRewards);
  console.log(landRewards);


  return (
    <Container fluid>
    <Row>
     <Col>
     <Table bsPrefix="custom-table" striped bordered hover responsive>
     <thead>
       <tr className="text-center">
         <td colSpan={3}>
           <h1>Player Reward Leaderboard</h1>
         </td>
       </tr>
     </thead>

     <thead>
       <tr>
         <th>Rank</th>
         <th>Account</th>
         <th>Amount</th>
       </tr>
     </thead>
     <tbody>
       {[...playerRewards]
         .sort((a, b) => b.amount - a.amount)
         .slice(0, 100)
         .map((item, idx) => {
           //let amount = item[1].toLocalString();
           return (
             <tr key={idx + "rank"}>
               <td>{idx + 1}</td>
               <td>
                 {item.account} / {item.p_id}
               </td>
               <td>
                 <NumberFormat
                   thousandSeparator={true}
                   displayType={"text"}
                   value={item.amount}
                   decimalScale={2}
                 />
               </td>
             </tr>
           );
         })}
     </tbody>
   </Table>
     </Col>
     <Col>
     <Table bsPrefix="custom-table" striped bordered hover responsive>
     <thead>
       <tr className="text-center">
         <td colSpan={3}>
           <h1>Region Reward Leaderboard</h1>
         </td>
       </tr>
     </thead>

     <thead>
       <tr>
         <th>Rank</th>
         <th>Account</th>
         <th>Amount</th>
       </tr>
     </thead>
     <tbody>
       {[...landRewards]
         .sort((a, b) => b.amount - a.amount)
         .slice(0, 100)
         .map((item, idx) => {
           //let amount = item[1].toLocalString();
           return (
             <tr key={idx + "rank"}>
               <td>{idx + 1}</td>
               <td>{item.p_id}</td>
               <td>
                 <NumberFormat
                   thousandSeparator={true}
                   displayType={"text"}
                   value={item.amount}
                   decimalScale={2}
                 />
               </td>
             </tr>
           );
         })}
     </tbody>
   </Table>
     </Col>
   </Row>

   

   
 </Container>
  );
}
