import { useEffect } from "react";

import "bootstrap/dist/css/bootstrap.min.css";
import Button from "react-bootstrap/Button";
import Card from "react-bootstrap/Card";
import Container from "react-bootstrap/Container";
import Table from 'react-bootstrap/Table';


import Row from 'react-bootstrap/Row';

import Col from 'react-bootstrap/Col';

import { Image } from "react-bootstrap";
import { run as runHolder } from "holderjs/holder";

import NumberFormat from 'react-number-format';

import React, { useState} from 'react'

import LiftiumPrice from './components/LiftiumPrice';

import './pages.css';


export default function LiftiumLeaders() {

  const [liftLeaders, initLiftLeaders] = useState([])
  const fetchData = async () => {
    const response = await fetch('https://www.api.bloks.io/wax/tokens?type=topHolders&chain=wax&contract=tokenizednft&symbol=LIFTIUM&limit=50')
    if (!response.ok) {
      throw new Error('Data coud not be fetched!')
    } else {
      return response.json()
    }
  }
  useEffect(() => {
    fetchData()
      .then((res) => {
        initLiftLeaders(res)
        //console.log(res);
       // liftiumArray = res;
      })
      .catch((e) => {
        console.log(e.message)
      })
  }, [])


useEffect(() => {
    runHolder("image-class-name");
  });
/*
  const liftiumHolderObject = {
    account:"",
    amount:0
}

const holderArray = [];

liftiumArray.forEach((holder, index)=>{
    const tHolder = Object.create(liftiumHolderObject);

    tHolder.account = holder[0];
    tHolder.amount = Number(holder[1]).toFixed(2).toLocaleString();

    holderArray[index] = tHolder;

});

let liftium = document.getElementById('liftium');


*/

console.log(liftLeaders);

  return (

<Container fluid>
       <Row>
        <Col>
        <Table bsPrefix="custom-table" striped bordered hover responsive>
    <thead>
        <tr>
          <th>#</th>
          <th>Account</th>
          <th>Amount</th>
        </tr>
      </thead>
      <tbody>
       
      {[...liftLeaders]
        .sort((a, b) => a[0] - b[0])
        .slice(0, 25).map((item,idx) => {
        //let amount = item[1].toLocalString();
        return (
          <tr key={idx+'rank'}>
          <td >{idx+1}</td>
          <td  >{item[0]}</td>
          <td  ><NumberFormat thousandSeparator={true} displayType={'text'} value={item[1]} decimalScale={2} /></td>
        </tr>
        )
      })}
         </tbody>
    </Table>
        </Col>
        <Col>
        <LiftiumPrice/>
       
        </Col>
      </Row>
   
      

      
    </Container>


   
    
  )
 
};
