import { Fragment, useEffect } from "react";
import Upliftium from "./upliftium";

import "bootstrap/dist/css/bootstrap.min.css";
import Button from "react-bootstrap/Button";
import Card from "react-bootstrap/Card";
import CardGroup from "react-bootstrap/CardGroup";
import Container from "react-bootstrap/Container";
import Table from "react-bootstrap/Table";

import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";

import { Image } from "react-bootstrap";
import { run as runHolder } from "holderjs/holder";

import NumberFormat from "react-number-format";

import React, { useState } from "react";

import './pages.css';

export default function Stats() {
  console.log(Upliftium.data);

  const [UpliftiumData, setUpliftiumState] = React.useState(
    Upliftium.data.data
  );

  useEffect(() => {
    runHolder("image-class-name");
  });
  return (
    <Container fluid>
  
      <Row className="justify-content-md-center">
        <Col>
          <Row className="justify-content-md-center">
            <Col>
              <CardGroup>
                <Card style={{ width: "18rem"}}  bsPrefix = "custom-card" className="text-center">
                  <Card.Body>
                    <Card.Title>Claimable</Card.Title>
                    <Card.Text>
                      <NumberFormat
                        thousandSeparator={true}
                        displayType={"text"}
                        value={Upliftium.data.payload.claimable}
                        decimalScale={0}
                      />
                    </Card.Text>
                  </Card.Body>
                </Card>
                <Card style={{ width: "18rem" }} bsPrefix = "custom-card" className="text-center">
                  <Card.Body>
                    <Card.Title>Crystallized</Card.Title>
                    <Card.Text>
                      <NumberFormat
                        thousandSeparator={true}
                        displayType={"text"}
                        value={Upliftium.data.payload.crystallized}
                        decimalScale={0}
                      />
                    </Card.Text>
                  </Card.Body>
                </Card>
              </CardGroup>
            </Col>
          </Row>
          <Row className="justify-content-md-center">
            <Col>
              <CardGroup>
                <Card style={{ width: "18rem" }} bsPrefix = "custom-card" className="text-center">
                  <Card.Body>
                    <Card.Title>Total Circulation</Card.Title>
                    <Card.Text>
                      <NumberFormat
                        thousandSeparator={true}
                        displayType={"text"}
                        value={Upliftium.data.payload.totalCirculation}
                        decimalScale={0}
                      />
                    </Card.Text>
                  </Card.Body>
                </Card>
                <Card style={{ width: "18rem" }} bsPrefix = "custom-card" className="text-center">
                  <Card.Body>
                    <Card.Title>Issued Supply</Card.Title>
                    <Card.Text>
                      <NumberFormat
                        thousandSeparator={true}
                        displayType={"text"}
                        value={Upliftium.data.payload.issuedSupply}
                        decimalScale={0}
                      />
                    </Card.Text>
                  </Card.Body>
                </Card>
              </CardGroup>
            </Col>
          </Row>
          <Row className="justify-content-md-center">
            <Col>
              <CardGroup>
                <Card style={{ width: "18rem" }} bsPrefix = "custom-card" className="text-center">
                  <Card.Body>
                    <Card.Title>Melted</Card.Title>
                    <Card.Text>
                      <NumberFormat
                        thousandSeparator={true}
                        displayType={"text"}
                        value={Upliftium.data.payload.melted}
                        decimalScale={0}
                      />
                    </Card.Text>
                  </Card.Body>
                </Card>
                <Card style={{ width: "18rem" }} bsPrefix = "custom-card" className="text-center">
                  <Card.Body>
                    <Card.Title>NFT Circulation</Card.Title>
                    <Card.Text>
                      <NumberFormat
                        thousandSeparator={true}
                        displayType={"text"}
                        value={Upliftium.data.payload.nftCirculation}
                        decimalScale={0}
                      />
                    </Card.Text>
                  </Card.Body>
                </Card>
              </CardGroup>
            </Col>
          </Row>
          <Row className="justify-content-md-center">
            <Col>
              <CardGroup>
                <Card style={{ width: "18rem" }} bsPrefix = "custom-card" className="text-center">
                  <Card.Body>
                    <Card.Title>Linked Accounts</Card.Title>
                    <Card.Text>
                      <NumberFormat
                        thousandSeparator={true}
                        displayType={"text"}
                        value={Upliftium.data.payload.numLinkedAccounts}
                        decimalScale={0}
                      />
                    </Card.Text>
                  </Card.Body>
                </Card>
                <Card style={{ width: "18rem" }} bsPrefix = "custom-card" className="text-center">
                  <Card.Body>
                    <Card.Title>On Balance Circulation</Card.Title>
                    <Card.Text>
                      <NumberFormat
                        thousandSeparator={true}
                        displayType={"text"}
                        value={Upliftium.data.payload.onBalanceCirculation}
                        decimalScale={0}
                      />
                    </Card.Text>
                  </Card.Body>
                </Card>
              </CardGroup>
            </Col>
          </Row>

          <Row className="justify-content-md-center">
            <Col>
              <CardGroup>
                <Card style={{ width: "18rem" }} bsPrefix = "custom-card" className="text-center">
                  <Card.Body>
                    <Card.Title>Portal Installs</Card.Title>
                    <Card.Text>
                      <NumberFormat
                        thousandSeparator={true}
                        displayType={"text"}
                        value={
                          Upliftium.data.payload.transactions.byType[7]
                            .numTransactions
                        }
                        decimalScale={0}
                      />
                    </Card.Text>
                  </Card.Body>
                </Card>
                <Card style={{ width: "18rem" }} bsPrefix = "custom-card" className="text-center">
                  <Card.Body>
                    <Card.Title>Picture Installs</Card.Title>
                    <Card.Text>
                      <NumberFormat
                        thousandSeparator={true}
                        displayType={"text"}
                        value={
                          Upliftium.data.payload.transactions.byType[6]
                            .numTransactions
                        }
                        decimalScale={0}
                      />
                    </Card.Text>
                  </Card.Body>
                </Card>
              </CardGroup>
            </Col>
          </Row>
        </Col>
      </Row>
    </Container>
  );
}
